/* General Section Styles */
.background-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 60px 20px;
}

.background-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(12px);
  width: 100%;
  max-width: 1200px;
  transition: all 0.3s ease;
}

.background-sections {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.background-section-left,
.background-section-right {
  width: 48%;
  padding: 20px;
}

.milestone-line {
  width: 2px;
  background-color: #f5a623;
  height: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.section-title {
  font-size: 2rem;
  font-weight: bold;
  color: white;
  border-bottom: 2px solid #f5a623;
  padding-bottom: 5px;
  margin-bottom: 1.5rem;
}

/* Education and Experience Item Styles */
.education-item,
.experience-item {
  margin-bottom: 1.5rem;
  cursor: pointer;
  padding: 10px;
  border-radius: 8px;
  transition: background-color 0.3s, transform 0.2s;
}

.education-item h4,
.experience-item h4 {
  font-size: 1.75rem; /* Slightly larger for better visibility */
  color: white;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.education-item p,
.experience-item p {
  font-size: 1.1rem; /* Slightly larger for readability */
  margin: 0.5rem 0;
  color: white;
}

.education-item:hover,
.experience-item:hover {
  background-color: #f5a52343;
  transform: scale(1.02);
}

.expanded-content {
  margin-top: 15px;
  font-size: 1.1rem; /* Slightly larger for better readability */
  color: white;
}

.expanded-content ul {
  list-style: disc;
  margin-left: 20px;
}

.expanded-content ul li {
  margin: 5px 0;
}

/* Title Styling */
.background-section h2 {
  color: white;
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .background-content {
    flex-direction: column;
    padding: 20px;
  }

  .background-sections {
    flex-direction: column;
    align-items: center;
  }

  .background-section-left,
  .background-section-right {
    width: 100%;
    padding: 10px;
  }

  .section-title {
    font-size: 1.5rem;
  }

  .education-item h4,
  .experience-item h4 {
    font-size: 1.25rem; /* Adjusting for smaller screens */
  }

  .education-item p,
  .experience-item p {
    font-size: 1rem; /* Scaling down a bit for small screens */
  }

  .expanded-content {
    font-size: 1rem;
  }

  .expanded-content ul {
    font-size: 1rem;
  }
}
